import {Component, OnInit} from '@angular/core';
import {Web3Service} from '../../util/web3.service';
import { MatSnackBar } from '@angular/material';

declare let require: any;
const simplewallet_artifacts = require('../../../../build/contracts/Simplewallet.json');

@Component({
  selector: 'app-simple-wallet',
  templateUrl: './simple-wallet.component.html',
  styleUrls: ['./simple-wallet.component.css']
})
export class SimpleWalletComponent implements OnInit {
  accounts: string[];
  SimpleWallet: any;

  model = {
    account: '',
    accountIsInWhitelist:false,
    depositAmount:0,
    balance:0,
    whitelistAddress:'',
    removeWhitelistAddress:'',
    sendToAddress:'',
    sendToAmount:0
  };

  status = '';

  constructor(private web3Service: Web3Service, private matSnackBar: MatSnackBar) {
    console.log('Constructor: ' + web3Service);
  }

  ngOnInit(): void {
    console.log('OnInit: ' + this.web3Service);
    console.log(this);
    this.watchAccount();
    this.web3Service.artifactsToContract(simplewallet_artifacts)
      .then((SimpleWalletAbstraction) => {
        this.SimpleWallet = SimpleWalletAbstraction;
        this.SimpleWallet.deployed().then(deployed => {
          console.log(deployed);
        });
      });
  }

  watchAccount() {
    this.web3Service.accountsObservable.subscribe((accounts) => {
      this.accounts = accounts;
      this.model.account = accounts[0];
      this.refreshBalance();
      this.checkIfIsInWhitelist();
    });
  }

  setStatus(status) {
    this.matSnackBar.open(status, null, {duration: 3000});
  }


  async refreshBalance() {
    console.log('Refreshing balance');

    try{
      const deployedSimpleWallet = await this.SimpleWallet.deployed();
      const simpleWalletBalance = await this.web3Service.getWeb3().then(web3 => {return web3.eth.getBalance(deployedSimpleWallet.address)});
      this.model.balance = this.SimpleWallet.web3.utils.fromWei(simpleWalletBalance, "Ether");
    }catch(e){
      console.log(e);
      this.setStatus("error to retrieve contracts balance, see logs");
    }
  }

  async checkIfIsInWhitelist(){
    console.log('Controllando se questo indirizzo è nella whitelist');

    try{
      const deployedSimpleWallet = await this.SimpleWallet.deployed();
      const isInWhiteList = await deployedSimpleWallet.isAllowedToSend(this.model.account);
      this.model.accountIsInWhitelist = isInWhiteList;
    }catch(e){
      console.log(e);
      this.setStatus("Errore, controlla i log");
    }
  }

  setDepositAmount(e){
    this.model.depositAmount = e.target.value;
  }

  setWhitelistAddress(e){
    this.model.whitelistAddress = e.target.value;
  }

  removeWhitelistAddress(e){
    this.model.removeWhitelistAddress = e.target.value;
  }

  setSendToAddress(e){
    this.model.sendToAddress = e.target.value;
  }

  setSendToAmount(e){
    this.model.sendToAmount = e.target.value;
  }

  async depositEther()
  {
    if(!this.SimpleWallet){
      this.setStatus("SimpleWallet non caricato, impossibile eseguire la transazione");
      return;
    }
    const amount = this.model.depositAmount;
    const sender = this.model.account;

    console.log("Depositando ether" + amount + "dall'indirizzo" + sender);
    this.setStatus("Transazione in corso...");
    try{
      const deployedSimpleWallet = await this.SimpleWallet.deployed();
      const transaction = await deployedSimpleWallet.sendTransaction({from: this.model.account, value: this.SimpleWallet.web3.utils.toWei(amount, "Ether")});
      if(!transaction){
        this.setStatus("Transazione fallita");
      }else{
        this.setStatus("Transazione completa!");
      }
      await this.refreshBalance();
    }catch(e){
      console.log(e);
      this.setStatus("Errore, guarda i log :(");
    }
  }

  async addAllowedAddress() {
    if (!this.SimpleWallet) {
      this.setStatus("SimpleWallet non caricato, impossibile eseguire la transazione");
      return;
    }
    const whitelistAddress = this.model.whitelistAddress;

    console.log('Aggiungendo alla whitelist il segunte indirizzo:' + whitelistAddress);

    this.setStatus('Inizializzando la transazione...');
    try {
      const deployedSimpleWallet = await this.SimpleWallet.deployed();
      const transaction = await deployedSimpleWallet.allowAddressToSendMoney(whitelistAddress, {from: this.model.account});

      if (!transaction) {
        this.setStatus('Transazione fallita!');
      } else {
        this.setStatus('Transazione completata!');
      }
    } catch (e) {
      console.log(e);
      this.setStatus('Errore guarda i log.');
    }
  }

  async removeAllowedAddress() {
    if (!this.SimpleWallet) {
      this.setStatus("SimpleWallet non caricato, impossibile eseguire la transazione");
      return;
    }
    const blacklistAddress = this.model.removeWhitelistAddress;

    console.log('Rimuovendo dalla whitelist il seguente indirizzo:' + blacklistAddress);

    this.setStatus('Transazione in corso...');
    try {
      const deployedSimpleWallet = await this.SimpleWallet.deployed();
      const transaction = await deployedSimpleWallet.disallowAddressToSendMoney(blacklistAddress, {from: this.model.account});

      if (!transaction) {
        this.setStatus('Transazione fallita!');
      } else {
        this.setStatus('Transazione completa!');
      }
    } catch (e) {
      console.log(e);
      this.setStatus('Errore, guarda i log.');
    }
  }

  async sendFromWallet()
  {
    if(!this.SimpleWallet){
      this.setStatus("SimpleWallet non caricato, impossibile eseguire la transazione");
      return;
    }
    const sendToAmount = this.model.sendToAmount;
    const sendToAddress = this.model.sendToAddress;

    console.log("Inviando" + sendToAmount + "ether all'indirizzo" + sendToAddress);
    this.setStatus("Inizializzando la transazione, aspettare...");
    try{
      const deployedSimpleWallet = await this.SimpleWallet.deployed();
      const transaction = await deployedSimpleWallet.sendFunds(this.SimpleWallet.web3.utils.toWei(sendToAmount, "Ether"), sendToAddress, {from: this.model.account});
      if(!transaction){
        this.setStatus("Transazione fallita!");
      }else{
        this.setStatus("Transazione completata!");
      }
      await this.refreshBalance();
    }catch(e){
      console.log(e);
      this.setStatus("Errore, guarda i logs");
    }
  }
}
